.bg-text {
    display: flex;
    position: relative;
    width: 600px;
    margin: 30px auto 30px;
    height: 350px;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.bg-img:nth-child(1) {

}

.bg-img:nth-child(2) { 
    -webkit-animation-delay: 6s;
    -moz-animation-delay: 6s;
    -o-animation-delay: 6s;
    -ms-animation-delay: 6s;
    animation-delay: 6s; 
}

.cb-slideshow,
.cb-slideshow:after { 
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    z-index: 0;
        padding-top: 57px; 
}
.cb-slideshow:after { 
    content: '';
   /* background: transparent url(/images/pattern.png) repeat top left; */
}
.cb-slideshow li span { 
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    color: transparent;
    background-repeat: no-repeat;
    opacity: 1;
    z-index: 0;
